<template>
    <div class="element-editor divider-form">

        <div class="editor-box">
            <h2>Divider</h2>
            <form-fields :fields="content" @changeElement="updateElementData" />
        </div>

        <div class="editor-box">
            <h2>Divider Style</h2>
            <form-fields :fields="styles" @changeElement="updateElementStyles" />
        </div>

    </div>
</template>

<script>
import FormFields from '@/components/controls/FormFields.vue';

export default {
    name: 'divider-form',
    props: [ 'element' ],
    components: { FormFields },
    data() {
        return {
            content: this.getContent(),
            styles: this.getStyles()
        }
    },
    watch: {
        element(element) {
            this.content = this.getContent();
            this.styles = this.getStyles();
        }
    },
    methods: {
        updateElementData(fieldName, newVal) {
            let newElement = JSON.parse(JSON.stringify(this.element));
            newElement.data[fieldName] = newVal;
            this.$emit('change', newElement);
        },
        updateElementStyles(fieldName, newVal) {
            let newElement = JSON.parse(JSON.stringify(this.element));
            newElement.styles[fieldName] = newVal;
            this.$emit('change', newElement);
        },
        getContent() {
            return [
                {
                    name: 'text',
                    cmp: 'input-text',
                    label: 'Text',
                    placeholder: 'Enter some text...',
                    default: 'Enter some text...',
                    rows: 5,
                    value: this.element.data.text
                }
            ]
        },
        getStyles() {
            return [
                {
                    name: 'borderTopStyle',
                    cmp: 'select',
                    label: 'Style',
                    options: [
                        { label: 'Solid', value: 'solid' },
                        { label: 'Double', value: 'double' },
                        { label: 'Dotted', value: 'dotted' },
                        { label: 'Dashed', value: 'dashed' },
                    ],
                    default: 'solid',
                    value: this.element.styles.borderTopStyle
                },
                {
                    name: 'borderTopColor',
                    cmp: 'color',
                    label: 'Color',
                    default: '#000000',
                    value: this.element.styles.borderTopColor
                },
                {
                    name: 'borderTopWidth',
                    cmp: 'input-range',
                    label: 'Size',
                    default: '1px',
                    min: '1',
                    max: '10',
                    unit: 'px',
                    value: this.element.styles.borderTopWidth
                },
                {
                    name: 'padding',
                    cmp: 'input-range',
                    label: 'Gap',
                    default: '0',
                    min: '0',
                    max: '50',
                    unit: 'px',
                    value: this.element.styles.padding
                },
                {
                    name: 'width',
                    cmp: 'input-range',
                    label: 'Width',
                    default: '100%',
                    min: '1',
                    max: '100',
                    unit: '%',
                    value: this.element.styles.width
                },
                {
                    name: 'textAlign',
                    cmp: 'select',
                    label: 'Text Align',
                    options: [
                        { label: 'Default', value: '' },
                        { label: 'Left', value: 'left' },
                        { label: 'Center', value: 'center' },
                        { label: 'Right', value: 'right' },
                    ],
                    default: '',
                    value: this.element.styles.textAlign
                }
            ]
        }
    }
};
</script>
