<template>
    <div class="element divider-element" :style="elementStyle">

        <hr :style="dividerStyle" />

        <element-actions v-if="isEditMode" :id="element._id"></element-actions>

    </div>
</template>

<script>
import ElementActions from '@/components/elements/ElementActions.vue';

export default {
    name: 'divider-element',
    props: [ 'element', 'isEditMode' ],
    components: { ElementActions },
    computed: {
        elementStyle() {
            let elementStyle = {};
            elementStyle.paddingTop = this.element.styles.padding;
            elementStyle.paddingBottom = this.element.styles.padding;
            elementStyle.textAlign = this.element.styles.textAlign;
            return elementStyle;
        },
        dividerStyle() {
            let dividerStyle = JSON.parse(JSON.stringify(this.element.styles));
            delete dividerStyle.padding;
            delete dividerStyle.textAlign;
            return dividerStyle;
        }
    }
};
</script>

<style scoped lang="scss">
.divider-element {
    line-height: 0;
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;

    hr {
        border: 0;
        border-top: 2px solid #000;
        width: 100%;
        display: inline-block;
    }
}
</style>
